import { request } from '@umijs/max';

// 封装常用方法
export let httpGet = (url: any, params: any) => {
  return request(url, {
    method: 'GET',
    params
  });
}

export let httpPost: any = (url: any, params: any) => {
  return request(url, {
    method: 'POST',
    params
  });
}

export let httpPut: any = (url: any, params: any) => {
  return request(url, {
    method: 'PUT',
    params
  });
}

export let httpDelete: any = (url: any, params: any) => {
  return request(url, {
    method: 'DELETE',
    params
  });
}

export const httpUpLoad = (url: string, fileDOM: any) => {
  let formData = new FormData();
  formData.append('file', fileDOM);
  return request(url, {
    method: 'POST',
    formData
  });
};
